@use "sass:map";

$media: (
  mobile: 720px,
);

@mixin media-breakpoint-down($name, $breakpoints: $media) {
  $max: breakpoint-max($name, $breakpoints);

  @if $max {
    @media (max-width: $max) {
      @content;
    }
  } @else {
    @content;
  }
}


@function breakpoint-max($name, $breakpoints: $media) {
  $max: map.get($breakpoints, $name);

  @return if($max and $max > 0, $max - 0.02, null);
}
