@use "src/utils/styles/mediaQuery" as media;

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.toboard {
  margin-top: 100px;
  width: 70%;
  text-decoration: none;
  color: #666666;
  font-size: 20px;
  font-weight: 800;
  cursor: pointer;

  &:hover {
    color: #99ccff;
  }
}

.detail {
  margin-top: 40px;
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  &__container {
    width: 100%;
    margin: 20px 0;
  }

  &__id {
    display: flex;
    align-items: center;
    font-size: 18px;
    font-weight: 600;

    &--icon {
      width: 20px;
      background-color: #0256ab;
      padding: 2px;
      margin-right: 5px;
      border-radius: 100%;
    }
  }

  &__title {
    font-size: 38px;
    font-weight: 700;
    margin: 20px 0;
  }

  &__viewcount {
    display: flex;
    align-items: center;
    margin-left: 5px;

    &--img {
      width: 20px;
      margin-right: 5px;
    }
  }

  &__photo {
    position: relative;
    width: 80%;
    margin-bottom: 10px;
    
    @include media.media-breakpoint-down(mobile) {
      width: 100%;
    }
  }
  &__content {
    height: 100%;
    font-size: 20 px;
    word-break: break-all;
    white-space: pre-wrap;
  }

  &__button {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    font-weight: 600;
    margin-bottom: 10px;

    &--modify {
      width: 70px;
      height: 40px;
      margin-right: 5px;
      border: none;
      border-radius: 5px;
      background: linear-gradient(to bottom, #15314d, #215586, #296aa7);
      color: #ffffff;
      cursor: pointer;

      &:hover {
        background: linear-gradient(to bottom, #07111a, #183f62, #205282);
      }
    }

    &--delete {
      width: 70px;
      height: 40px;
      margin-right: 5px;
      border: none;
      border-radius: 5px;
      background-color: #ffffff;
      color: #222222;
      border: 1px solid #666666;
      cursor: pointer;

      &:hover {
        background-color: #e9e9e9;
        color: #222222;
      }
    }
  }
}
